@import'../common/variables';

.tabs {
  display: flex;
  width: 100%;
  font-size: 0;

  button {
    position: relative;
    width: 100%;
    height: 70px;
    // padding-bottom: 21px;
    font-size: 1.8rem;
    font-weight: 400;
    color: var(--cbb);

    &::before {
      @include before_after;
      position: absolute;
      width: 100%;
      height: 1px;
      bottom: 0;
      background-color: var(--cdd);
    }

    &.active {
      font-weight: 700;
      color: var(--c11);

      &::before {
        height: 3px;
        background-color: #0F0E12;
      }
    }
  }
  &_content{
    display: none;
    &.active{
      display: block;
    }
  }
}

.round {
  overflow-x: auto;
  padding: 0 16px;
  gap: 5px;

  button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: max-content;
    height: 35px;
    padding: 10px 20px;
    border: 1px solid var(--ce7);
    border-radius: 999px;
    @include body1(var(--c77));

    &::before {
      display: none;
    }

    &.active {
      border-color: #0F0E12;
      color: var(--c11);
    }
  }
}